import chroma from 'chroma-js';

const levels = [50, 100, 200, 300, 400, 500, 600, 700, 800, 900];
// var levels = [];
// for (var i = 50; i<=900; i++){
//   levels.push(i);
// }

function generatePalette(basePalette) {
  let newPalette = {
    paletteName: basePalette.paletteName,
    id: basePalette.id,
    emoji: basePalette.emoji,
    colors: {}
  };
  for (let level of levels) {
    newPalette.colors[level] = [];
  }
  for (let color of basePalette.colors) {
    let scale = generateScale(color.color, levels.length).reverse();
    for (let i in scale) {
      newPalette.colors[levels[i]].push({
        name: `${color.name} ${levels[i]}`,
        id: color.name.toLowerCase().replace(/ /g, ""),
        hex: scale[i],
        rgb: chroma(scale[i]).css(), // convert to rgb
        rgba: chroma(scale[i])       // convert to rgba
          .css()
          .replace("rgb", "rgba")
          .replace(")", ",1.0)")
      });
    }
  }
  return newPalette;
}

function getRange(hexColor) {
  const end = "#fff";
  return [
    chroma(hexColor).darken(1.4).hex(),
    hexColor,
    end
  ];
}

function generateScale(hexColor, numberOfColors) {
  return chroma.scale(getRange(hexColor)).mode("lab").colors(numberOfColors);
}

export { generatePalette }